import Promised from "../../../Arch/Promised";
import Timing from "../../../Browser/Timing";
import * as Common from "../../../Common";
import Ad from "../Base/Ad";

export default class AdDisplayRequest extends Promised {
    constructor(serviceContainer, ad) {
        super();
        this.ad = ad;
        this.id = ad.id;
        this.data = {
            id: null,
            requestTimestamp: Timing.timestamp(), //
            requestTime: Timing.relative(),
            requestSentTime: null,
            requestSentLocalTime:null,
            loadTime: null,
            loadLocalTime: null,
            displayStartTime:null,
            displayStartLocalTime:null,
            displayTime: null,
            displayLocalTime: null,
            requestToDisplayTime:null,

            impression:false,
            impressionTime:null,
            impressionLocalTime:null,

            requestedSizes: null,

            viewable: false,
            viewableTime: null,
            viewableLocalTime: null,

            maxViewability: 0,
            errorCount:0,
            lastErrorCode:0,
            lastErrorMessage:0,

            lineItemId: null,
            advertiserId: null,
            isEmpty: true,
            campaignId: null,
            creativeId: null,
            receivedSize: null,

            innerAdUnit: typeof window._SMCMacroSite === "undefined" ? "Unknown" : window._SMCMacroSite,
            finished: null
        };
        var sizes = this.ad.getSizes();
        if(typeof sizes[0]==="number")
            sizes=[sizes];

        this.data.requestedSizes = sizes.map(function (i) {
            if (Common.isArray(i))
                return i[0] + 'x' + i[1];
            if (Common.isString(i))
                return i;
            return ""
        }).sort().join(",");

    }

    toRelative() {
        return Timing.relative() - this.data.requestTime;
    }

    onRequested(event) {
        this.data.requestSentTime = Timing.relative();
        this.data.requestSentLocalTime = this.toRelative();
    }
    onLoaded(event)
    {
        this.data.loadTime= Timing.relative();
        this.data.loadLocalTime = this.toRelative();
        this.ad.onLoaded();
    }

    onViewable(event) {
        this.data.viewable = true;
        this.data.viewableTime = Timing.relative();
        this.data.viewableLocalTime = this.toRelative();
        this.ad.fireEvent(Ad.EVENT_SLOT_VISIBLE, {ad: this.ad});
    }

    onDisplayStart(event) {
        this.data.displayStartTime=Timimng.relative();
        this.data.displayStartLocalTime=this.toRelative();
    }

    onDisplayEnded(event) {
        this.ad.setDisplayed();
        this.data.displayTime = Timing.relative();
        this.data.displayLocalTime = this.toRelative();
        this.data.requestToDisplayTime=this.data.displayLocalTime - this.data.requestSentLocalTime;

    }

    onEmpty() {
        this.data.isEmpty = true;
        this.ad.log("EMPTY");
        this.ad.fireEvent(Ad.EVENT_AD_EMPTY, {ad: this.ad});
    }

    onAdReceived(advertiser, campaign, creative, lineItem) {
        this.data["advertiserId"] = advertiser;
        this.data["campaignId"] = campaign;
        this.data["creativeId"] = creative;
        this.data["lineItemId"] = lineItem;
        this.ad.fireEvent(Ad.EVENT_AD_AVAILABLE, {slot: this.ad});
    }
    onSlotVisibilityChange(percent)
    {
        this.ad.fireEvent(Ad.EVENT_VISIBILITY_CHANGE,{ad:this.ad,visibility:percent});
    }
    onImpression()
    {
        this.data.impression=true;
        this.data.impressionTime=Timing.relative();
        this.data.impressionLocalTime=this.toRelative();
    }
    addProperty(key, value) {
        this.data[key] = value;
    }
    onError()
    {

    }
    finish() {
    }
}